/* ==========================================================================
   Design System
   Notifications Styling
   ========================================================================== */
.m-notification {
  display: none;
  position: relative;
  padding: 15px;
  background: var(--gray-5);
  border: 1px solid var(--gray-40);
}
.m-notification > .cf-icon-svg {
  position: absolute;
  fill: var(--gray);
}
.m-notification__success {
  background: var(--green-10);
  border-color: var(--green);
}
.m-notification__success .cf-icon-svg {
  fill: var(--green);
}
.m-notification__warning {
  background: var(--gold-10);
  border-color: var(--gold);
}
.m-notification__warning .cf-icon-svg {
  fill: var(--gold);
}
.m-notification__error {
  background: var(--red-10);
  border-color: var(--red);
}
.m-notification__error .cf-icon-svg {
  fill: var(--red);
}
.m-notification__visible {
  display: block;
}
.cf-icon-svg + .m-notification_content {
  padding-left: 1.5625em;
}
.m-notification_message {
  margin-bottom: 0;
}
.m-notification_explanation {
  margin-top: 0.3125em;
  margin-bottom: 0.9375em;
}
.m-notification_message + .m-list {
  margin-top: 0.9375em;
}
@media only all and (min-width: 37.5625em) {
  .m-notification .cf-icon-svg {
    font-size: 1.125em;
  }
  .m-notification_explanation {
    margin-bottom: 0;
  }
  .m-notification .m-list {
    margin-top: 0.3125em;
  }
  .m-notification .m-list_item {
    margin-bottom: 0.3125em;
  }
}
html[lang='ar'] .m-notification .cf-icon-svg + .m-notification_content {
  padding-left: initial;
  padding-right: 1.5625em;
}
html[lang='ar'] .m-notification .m-list {
  padding-right: 0;
}
.o-banner {
  padding: 0.9375em 0;
  background: var(--gold-10);
  border-bottom: 1px solid var(--gray-40);
  font-size: 0.875em;
}
.o-banner .m-notification {
  border: none;
  padding: 0;
}
.o-banner .m-notification_icon {
  left: 0;
  top: 0;
}
.o-banner__dark {
  background: var(--teal-mid-dark);
  border-color: var(--teal-mid-dark);
  color: var(--white);
}
.o-banner__dark a {
  border-color: var(--white);
  color: var(--white);
}
.o-banner__dark a:hover {
  border-color: var(--gray-15);
  color: var(--gray-15);
}
.o-banner__dark .m-notification {
  background: var(--teal-mid-dark);
}
.o-banner__dark .m-notification .cf-icon-svg {
  fill: var(--white);
}
@media only all and (min-width: 37.5625em) {
  .o-banner {
    font-size: 1em;
  }
}
